
.search-field {
  width: 40%;
}
.button {
  display: inline-block;
  width: 130px;
}
#secret-list > li {
  width: 70%;
  max-width: 600px;
  margin: 0 auto;
  margin-bottom: 10px;
  background-color: rgba(15, 15, 15, 0.015);
  border-radius: 5px;
  border: 1px solid transparent;
}
#secret-list > li .secret-title, #secret-list > li .secret-price {
    opacity: 0.6;
}
#secret-list > li:hover {
    background-color: #494e75;
}
#secret-list > li:hover .secret-title, #secret-list > li:hover .secret-price, #secret-list > li:hover a {
      opacity: 1;
      color: white;
}
.secret-listing-actions {
  margin-bottom: 20px;
}
.placeholder {
  margin-bottom: 20px;
}
input, textarea {
  width: 40%;
  max-width: 400px;
}



