
@-webkit-keyframes blink {
50% {
    color: transparent;
}
}
@keyframes blink {
50% {
    color: transparent;
}
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  height: 100%;
  color: #2c3e50;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
#app .app-header {
    border-bottom: 1px solid #ddd;
    padding: 15px;
    margin: 15px 15%;
    font-weight: bold;
    display: block;
    opacity: 0.8;
    font-size: 40px;
}
#app .app-header .process-loader span {
      -webkit-animation: 1.5s blink infinite;
              animation: 1.5s blink infinite;
}
#app .app-header .process-loader span:nth-child(2) {
      -webkit-animation-delay: 0.5s;
              animation-delay: 0.5s;
}
#app .app-header .process-loader span:nth-child(3) {
      -webkit-animation-delay: 1s;
              animation-delay: 1s;
}
#app .app-wrapper {
    width: 70%;
    margin: 0 auto;
}
#app .app-footer {
    margin: 0 auto;
    width: 70%;
}
#app .app-footer .contract-address-footer {
      padding: 15px 15px 10px 15px;
      margin: 15px 15%;
      border-top: 1px solid #ddd;
}
#app ul {
    padding: 0;
    list-style-type: none;
}
#app li {
    list-style: none;
}
#app .button {
    border: 1px solid #494e75;
    border-radius: 4px;
    padding: 5px 15px;
    color: #494e75;
    text-decoration: none;
    -webkit-transition: all 0.3;
    transition: all 0.3;
}
#app .button.left {
      border-radius: 0 5px 5px 0;
}
#app .button.right {
      border-radius: 5px 0 0 5px;
}
#app .button:focus {
      outline: none;
}
#app .button:hover {
      background-color: #494e75;
      color: #fff;
}
#app .button:active {
      background-color: #fff;
      color: #494e75;
}
#app input, #app textarea {
    padding: 8px;
    font-size: 15px;
    resize: none;
    border: none;
    border-bottom: 1px solid #ddd;
}
#app input:focus, #app textarea:focus {
      outline: none;
      border-bottom: 1px solid #494e75;
}
#app input::-webkit-input-placeholder, #app textarea::-webkit-input-placeholder {
      -webkit-transition: opacity 0.4s;
      transition: opacity 0.4s;
      opacity: 0.8;
}
#app input:focus::-webkit-input-placeholder, #app textarea:focus::-webkit-input-placeholder {
      opacity: 0.2;
}
html, body {
  height: 100%;
  margin: 0px;
}



